
  
  h1 {
    text-align: center;
  }
  h2 {
    text-align: center;
  }
  
  
  .cards__wrapper3 {
    position: relative;
    align-items: center;
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    margin: 0 auto;

  }
  
  .cards__items3 {
    margin-bottom: 60px;
    margin-top: -100px;
    max-width: 800px;
  }
  
  
  .cards__item3 {
    display: flex;
    flex: 3;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link3 {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap3 {
    position: relative;
    width: 100%;
    padding-top: 100%;
    overflow: hidden;
  }
  
  .fade-img3 {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap3::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%));
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .cards__item__img3 {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img3:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info3 {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text3 {
    color: #050050;
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container3 {
      width: 84%;
    }
    .cards__container3{
      width: 70%;
    }
    .cards__container3{
      width: 70%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items3 {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item3 {
      margin-bottom: 2rem;
      align-self: center;
      width: 450px;
    }
    .cards__container3{
      width: 30%;
    }
  }
  @media only screen and (max-width: 724px) {
    .cards__item3 {
      margin-bottom: 2rem;
      align-self: center;
      width: 375px;
    }
    .cards__container3{
      width: 20%;
    }
  }