
  .form-container7 {
    margin: 100px auto;
    max-width: 375px;
    position: relative;
    border-radius: 10px;
    height: 650px;
    display: flex;
    align-self: center;
    grid-template-columns: 1fr 1fr;
    background: #050050;
  }
  
  .form-success {
    text-align:start;
    font-size: 24px;
    margin-top: 80px;
    color: #fff;
  }
  
  .form-content7 {
    border-radius: 10px 10px 10px 10px;
    position: center;
  }
  
  .form7 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .form7 h1 {
    font-size: 35px;
    text-align: center;
    width: 100%;
    margin-bottom: 2rem;
    color: #fff;
  }

  .form7 h3 {
    font-size: 21px;
    text-align: center;
    width: 100%;
    color: #fff;
    margin-top:-14px;
  }
  .form7 h3:hover {
    color: #050050;
  }

  .form7 h2 {
    font-size: 18px;
    text-align: center;
    width: 100%;
    color: #fff;
  }
  
  .form7 p{
    font-size: 15px;
    text-align:start;
    width: 100%;
    justify-content: center;
    margin-bottom: 1rem;
    color: #fff;
  }
  .form-inputs {
    margin-bottom: 0.5rem;
    width: 80%;
  }
  
  .form-inputs p {
    align-items: center;
    font-size: 0.8rem;
    margin-top: 0rem;
    color: #f00e0e;
  }
  
  .form-label {
    display: inline-block;
    font-size: 1rem;
    margin-bottom: 6px;
    color: #fff;
  }
  
  .form-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 40px;
    width: 100%;
    border: none;
  }
  
  .form-input::placeholder {
    color: #595959;
    font-size: 12px;
  }
  
  
  .form-input-login {
    font-size: 0.8rem;
    margin-top: 10px;
    color: #fff;
    width: 80%;
    text-align: center;
  }
  
  .form7 a{
    text-decoration: none;
  }
  
  .form-input-btn {
    display: block;
    text-align: center;
    margin: 1rem auto;
    border-radius: 10px;
    width: 100%;
    text-decoration: none;
    background-color: transparent;
    color: #fff;
    padding: 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}
.form-input-btn:hover {
  background: #fff;
  color: #050050;
  transition: 250ms;
}