.form-container {
    margin: 100px auto;
    width: 375px;
    position: relative;
    border-radius: 10px;
    height: 675px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: #050050;
  }
  .form-container2 {
    margin: 100px auto;
    width: 375px;
    position: relative;
    border-radius: 10px;
    height: 1250px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: #050050;
  }
  
  
  .form-success {
    text-align: center;
    font-size: 24px;
    margin-top: 80px;
    color: #fff;
  }
  
  .form-content {
    border-radius: 10px 10px 10px 10px;
    position: center;
  }
  
  .form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .form h1 {
    font-size: 50px;
    text-align: center;
    width: 80%;
    margin-bottom: 2rem;
    color: #fff;
  }
  
  .form p{
    font-size: 20px;
    text-align: center;
    width: 80%;
    margin-bottom: 1rem;
    color: #fff;
  }
  .form-inputs {
    margin-bottom: 0.5rem;
    width: 80%;
  }
  
  .form-inputs p {
    align-items: center;
    font-size: 0.8rem;
    margin-top: 0rem;
    color: #f00e0e;
  }
  
  .form-label {
    display: inline-block;
    font-size: 1rem;
    margin-bottom: 6px;
    color: #fff;
    margin-left: 10px;
  }
  
  .form-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 40px;
    width: 100%;
    border: none;
    
  }
  
  .form-input::placeholder {
    color: #595959;
    font-size: 12px;
  }
  
  
  .form-input-login {
    font-size: 0.8rem;
    margin-top: 10px;
    color: #fff;
    width: 80%;
    text-align: center;
  }
  
  .form-input-login a {
    text-decoration: none;
    color: #27cdff;
    font-weight: 600;
  }