
video {
  position: fixed;
  right: 0;
  top: 0px;
  min-width: 100%;
  height: 100%;
}
.events-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  object-fit: contain;
  margin-bottom: 25px;
}


.event-box .info{
  position: absolute;
  top: 25px;
  pointer-events: none;
}
.event-box .info-content{
  position: absolute;
  top: 0px;
  bottom: -60px;
  pointer-events: none;
  transition: 2s;
  opacity:1;
  animation: 24s coolAnimation2 infinite;
  animation-timing-function: linear;
}
.event-box .timeline{
  position: absolute;
  bottom: 25px;
  pointer-events: none;
}



.event-box {
  position: relative;
  color: #050050;
  width: 350px;
  height: 450px;
  margin: 25px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px 15px 0px 0px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  transition: top ease 1s;
}

.events-button {
  position: relative;
  color: #050050;
  width: 350px;
  height: 50px;
  margin-top: -25px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 0px 0px 15px 15px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  transition: top ease 1s;
  text-decoration: none;
}


.events-container > h1 {
  color: #050050;
  font-size: 50px;
}


@media screen and (max-width: 1024px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
  .event-box{
    margin: 0px;
    margin-bottom: 25px;
    align-self: center;
  }
  .events-button{
    margin-bottom: 25px;
    align-self: center;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}

@keyframes coolAnimation2 {
  0% {
    transform: rotate(0deg) translateY(50vh);
  }
}