.navbar {
  background: #050050;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  position: sticky;
  top: 0px;
  z-index: 999;
}
.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 2500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 0px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.img-responsive{
  height: 75px;
  width: 75px;
  position: absolute;
  justify-self: start;
  margin-left: 0px;
  left: 0px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.alma-img{
  height: 50px;
  position: absolute;
  justify-self:center;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.img2{
  height: 25px;
  width: 100px;
  position: absolute;
  margin-left: 400px;
  left: 0px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}
.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 7px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
}

.nav-item {
  height: 100%;
  color: rgb(255, 196, 0);
}

.nav-item3{
  height: 100%;
  color: rgb(255, 196, 0);
}

.nav-txt1 {
  color: #fff;
  display: flex;
  height: 60px;
  margin-left: 50px;
}
.nav-txt2 {
  color: #fff;
  display: flex;
  height: 0px;
  margin-left: -328px;
}
.nav-txt3 {
  color: rgb(255, 196, 0);
  display: flex;
  height: 0px;
  margin-left: 5px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 1rem;
  height: 100%;
}

.nav-links2 {
  color: rgb(255, 196, 0);
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 1rem;
  height: 100%;
  animation: blinker 1.5s linear infinite;
}

@keyframes blinker {
  50% {
    color: #050050;
  }
}
.nav-links:hover {
  color:rgb(255, 196, 0);
  transition: all 0.2s ease-out;
}

.nav-links2:hover {
  color:#fff;
  transition: all 0.2s ease-out;
}


.fa-bars {
  color: #fff;
}

.nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    margin-top: 35px;
    border-radius: 10px;
    width: 150%;
    text-decoration: none;
    background-color: transparent;
    color: #fff;
    padding: 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}
.nav-links-mobile:hover {
  background: #fff;
  color: #050050;
  transition: 250ms;
}
.menu-icon {
  display: none;
}

.nav-txt4{
  color: #fff;
  height: 60px;
  margin-left: -30px;
  display: none;
}
.nav-txt5{
  color: #fff;
  display: none;
  height: 0px;
  margin-left: -200px;
}

@media screen and (max-width: 1350px) {
  .NavbarItems {
    position: relative;
  }
  
  .nav-txt4{
    display: flex;
  }
  .nav-txt5{
    display: flex;
  }

  .nav-txt1{
    display: none;
  }
  .nav-txt2{
    display: none;
  }
  .nav-txt3{
    display: none;
  }
  .alma-img{
    display:none;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #050050;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 1rem;
    width: 100%;
    display: table;
  }
  .nav-links2 {
    text-align: center;
    padding: 1rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #050050;
    border-radius: 0;
  }
  .nav-links2:hover {
    background-color: #fff;
    color: #050050;
    border-radius: 0;
  }
  
  .img-responsive{
    top: -35px;
    left: 0;
    position: absolute;
    transform: translate(25%, 50%);
    
  }
  .img2{
    top: -35px;
    left: 50px;
    position: absolute;
    transform: translate(25%, 50%);
  }
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 1rem auto;
    margin-top: -10px;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5 rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #050050;
    transition: 250ms;
  }
}
