.event-box-container{
  position:sticky;
  color: #050050;
  max-width: 700px;
  height: 300px;
  width: 95%;
  margin: 10px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  transition: top ease 0.5s;
  align-self: center;
  margin-bottom: 250px;
}

.event-box-img{
  width: 300px;
  border-radius: 5px;
  height: 300px;
  align-self: center;
}

.event-box-container .info{
  position: absolute;
  top: 0px;
  pointer-events: none;
  margin-left: 300px;
  font-size: 25px;
}

.event-box-container .timeline{
  position: absolute;
  top: 80px;
  margin-left: 300px;
  font-size: 20px;
  pointer-events: none;
}
.event-box-container .desc {
  text-align:justify;
  transform: translateY(75px);
  font-size: 15px;
  padding: 3px;
  margin-top: 45px;
   
}

.event-box1:hover .desc{
transform: translateY(0px);
          opacity: 1;
}

@media screen and (max-width: 750px) {
  .event-box-container{
    height: 200px;
  }
  .event-box-img{
    height: 200px;
    width: 200px;
  }
  .event-box-container .info{
    margin-left: 200px;
    font-size: 15px;
  }

  .event-box-container .timeline{
    margin-left: 200px;
    font-size: 12px;
  }

  .event-box-container .desc{
    font-size: 11px;
  }

}