.cards1 {
    padding: 4rem;
    background: #ffffff;
  }
  
  h1 {
    text-align: center;
  }
  h2 {
    text-align: center;
  }
  
  .cards__container1 {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards__wrapper1 {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items1 {
    margin-bottom: 24px;
    width: 250px;
  }
  
  .cards__item1 {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link1 {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .fade-img1 {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  
  .cards__item__img1 {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img1:hover {
    transform: scale(1.1);
  }
  .cards__item__pic-wrap1 {
    position: relative;
    width: 100%;
    padding-top: 100%;
    overflow: hidden;
  }
  .cards__item__info1 {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text1 {
    color: #050050;
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container1 {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items1 {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item1 {
      margin-bottom: 2rem;
    }
  }
  