.footer-container123 {
  background-color: #050050;
  padding: 2rem 0 2rem 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: sticky;
}

.footer-subscription {
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
  padding: 25px;
  color: #fff;
}

.footer-subscription > p {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.footer-subscription-heading {
  font-size: 25px;
}


/* Social Icons */
.social-icon-link {
  color: #fff;
  font-size: 25px;
}

.social-media {
  width: 99%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 250px;
}
.footer-logo{
  width: 250px;
}

.social-logo {
  color: #fff;
  margin-left: 25px;
  margin-right: 25px;
  display: relative;
  justify-content: center;

}

.almaconnect-logo{
  max-height: 75px;
  margin-left: 25px;
  margin-right: 25px;
}

.website-rights {
  color: #fff;
  margin-left: 25px;
  margin-right: 2px;
}

@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .footer-logo{
    margin-left: -150px;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .social-logo {
    color: #fff;
    margin-left: 105px;
    display: relative;
    justify-content: center;
    margin-bottom: 25px;
  
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
  .website-rights {
    color: #fff;
    margin-bottom: 16px;
    align-self: center;
    margin-left: 0px;
  }
  .social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
    margin-right: 0px;
  }
  .footer-logo{
    margin-bottom: 15px;
  }
}

