h1{
    text-align: center;
}

.about-container{
    margin: 100px auto;
    padding: 2rem;
    margin: 100px auto;
    position: relative;
    border-radius: 10px;
    height: 600px;
}

h2{
    padding: 1rem;
}
h3{
    padding: 1rem;
    text-align: center;
    text-align: justify;
}
h5{
    padding: 1.5rem;
    font-size: 35px;
    text-align: center;
    text-decoration: none;
}
h6{
    padding: 1.5rem;
    font-size: 21px;
    text-align: center;
    text-decoration: none;
    text-align: justify;
}
h4{
    text-align: center;
    text-align: justify;
}

.members-text{
    text-decoration: none;
}

.p1{
    text-align: center;
    text-align: justify;
}

@media screen and (max-width: 675px) {
    .about-container{
        height: 800px;
    }
}

@media screen and (max-width: 500px) {
    .about-container{
        height: 1000px;
    }
}

@media screen and (max-width: 380px) {
    .about-container{
        height: 1100px;
    }
}